<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-container fluid style="max-width: 80%">
          <v-form v-model="valid">
            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Get match</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-select
                  :items="matches"
                  v-model="currentMatch"
                  label="Select the match"
                  single-line
                ></v-select>
              </v-flex>
            </v-layout>
            <v-btn :disabled="!valid" @click="startMatch" block dark style="margin-bottom: 20px">Start Match</v-btn>
          </v-form>
        </v-container>
        
        <v-container fluid style="max-width: 80%">
          <v-divider></v-divider>
          <v-form>
            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Team</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-select
                  :items="teams"
                  v-model="currentTeam"
                  label="Select the Team"
                  single-line
                  @change="teamChange"
                ></v-select>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Score</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-text-field v-model="score" type="number" style="padding-left: 10px; padding-right: 10px"></v-text-field>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Wickets</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-text-field v-model="wickets" type="number" style="padding-left: 10px; padding-right: 10px"></v-text-field>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Overs</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-text-field v-model="overs" type="text" style="padding-left: 10px; padding-right: 10px"></v-text-field>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Wide Balls</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-text-field v-model="wide" type="number" style="padding-left: 10px; padding-right: 10px"></v-text-field>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>No Balls</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-text-field v-model="noBalls" type="number" style="padding-left: 10px; padding-right: 10px"></v-text-field>
              </v-flex>
            </v-layout>

            <v-btn color="error" @click="update" block>Update</v-btn>
          </v-form>
        </v-container>

        <v-container fluid style="max-width: 80%">
          <v-form v-model="valid">
            <v-layout row wrap>
              <v-flex xs6>
                <v-subheader>Winner</v-subheader>
              </v-flex>
              <v-flex xs6>
                <v-select
                  :items="teams"
                  v-model="winningTeam"
                  label="Select the Team"
                  single-line
                ></v-select>
              </v-flex>
            </v-layout>
            <v-btn :disabled="!valid" @click="updateWinner" block dark style="margin-bottom: 20px">Update</v-btn>
          </v-form>
        </v-container>

      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<script>
  var socket = require('socket.io-client')('/');

  export default {
    data () {
      return {
        valid: true,
        matches:[],
        currentMatch:'',
        winningTeam: '',
        currentTeam: '',
        teams: [],
        wide: 0,
        noBalls: 0,
        score: 0,
        overs: '0.0',
        wickets: 0
      }
    },
    name: 'StartMatch',
    methods:{
      startMatch(){
        socket.emit("getTeamDetails", this.currentMatch, (err, res) =>{
          if(err){
            return console.log(err);
          }
          this.teams = [];
          res.forEach((team) => {
            this.teams.push({
              text: team.name,
              value: team._id
            });
          });
        })
      },
      teamChange(e) {
        socket.emit("getTeamScore", e, this.currentMatch, (err, res) =>{
          if(err){
            return console.log(err);
          }
          this.score = res.score;
          this.wickets = res.wicket;
          this.overs = res.overs;
          this.wide = res.wide;
          this.noBalls = res.no;
          this.currentTeam = e;
        })
      },
      update() {
        socket.emit("updateTeamScore", this.currentMatch, this.currentTeam, {
          score: this.score,
          wicket: this.wickets,
          overs: this.overs,
          wide: this.wide,
          no: this.noBalls
        }, (err, res) =>{
          if(err){
            return console.log(err);
          }
        })
      },
      updateWinner(){
        const lostTeam = this.teams.filter((team) => team.value != this.winningTeam)[0].value;
        socket.emit("setWinner", this.currentMatch, this.winningTeam, lostTeam, (err) =>{
          if(err){
            return console.log(err);
          }
          alert("Match Completed");
          window.location = "/";
        })
      }
    },
    mounted(){
      socket.emit("getLiveMatchesOnly", (err, res)=>{
        if(err){
          return console.log(err);
        }
        res.forEach((match) => {
          this.matches.push({
            text: match.name,
            value: match._id 
          });
        });
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>



